<template>
  <div ref="RCMenu" class="right_click_menu glass" :style="pst" v-if="show" @click.stop="close">
    <router-link v-for="(v, i) in menus" :key="i" :to="v.path" tag="div">{{ v.label }}</router-link>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      menus: [],
      pst: {
        top: "0px",
        left: "0px"
      }
    }
  },
  watch: {
    show(val) {
      if(val) {
        document.addEventListener("click", this.close)
      } else {
        document.removeEventListener("click", this.close)
      }
    }
  },
  mounted() {
    document.addEventListener("contextmenu", (e) => {
      this.pst.top = e.y + "px";
      this.pst.left = e.x + "px";
      this.show = true;
      e.preventDefault();
    })

    const routes = this.$router.options.routes;
    routes.forEach(v => {
      if(v.meta?.title) this.menus.push({
        label: v?.meta.title,
        path: v.path
      })
    });
  },
  methods: {
    close() {
      this.show = false;
    }
  }
}
</script>
<style lang="less" scoped>
.right_click_menu {
  font-family: shaonv;
  min-width: 120px;
  border-radius: 8px;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, .1);
  background: rgba(255, 255, 255, .3);
  color: #c0c0c0;
  position: fixed;
  cursor: pointer;
  div {
    padding: 5px 10px;
    &:first-child {
      padding-top: 10px;
    }
    &:last-child {
      padding-bottom: 10px;
    }
  }
}
</style>