import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

function $(route) {
  return import(`@/pages/${route}`)
}
const routes = [
  {
    path: "/",
    meta: {
      title: "导航"
    },
    component: () => import('@/pages/index'),
  },
  {
    path: "/photo_wall",
    meta: {
      title: "图片"
    },
    component: () => import('@/pages/photoWall'),
  },
  {
    path: "/octopath_traveler",
    name: "ot",
    meta: {
      title: "八方旅人"
    },
    component: () => import('@/components/Layout/index.vue'),
    children: [
      {
        path: "/",
        name: "otIndex",
        component: () => import('@/pages/octopathTraveler/index.vue'),
      }
    ]
  },
  {
    path: "/home",
    meta: {
      title: "首页"
    },
    component: () => import('@/components/Layout/index.vue'),
    children: [
      {
        path: "/",
        name: "home",
        component: () => import('@/pages/home.vue'),
      }
    ]
  },
  {
    path: "*",
    component: (resolve) => require(["@/pages/404"], resolve),
  }
]
const routerEx = new Router({
  routes
});

export default routerEx;